@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");
body.dark {
  font-family: "Inter";
  background-color: var(--dark-bg-color);
  color: var(--dark-text-color);
}
body.dark .toggle-top,
body.dark .toggle-center,
body.dark .toggle-bottom {
  background-color: var(--dark-text-color);
}
body.dark .about {
  background-color: var(--dark-bg-color);
}
body.dark .theme-toggle__left,
body.dark .theme-toggle__right {
  border: 1px solid var(--dark-text-color);
  cursor: pointer;
}
body.dark a {
  text-decoration: none;
  color: var(--dark-text-color);
}
body.dark a:hover {
  text-decoration: underline;
}
body.dark span.name {
  color: var(--dark-text-color);
}
body.dark ul {
  list-style: none;
}
body.dark ul li {
  color: var(--dark-text-color);
}
body.dark header,
body.dark main,
body.dark footer {
  background: var(--dark-bg-color);
  color: var(--dark-text-color);
}
body.dark main,
body.dark footer {
  width: 80%;
  margin-left: 10%;
}

body.light {
  font-family: "Inter";
  background-color: var(--light-bg-color);
  color: var(--light-text-color);
}
body.light .toggle-top,
body.light .toggle-center,
body.light .toggle-bottom {
  background-color: var(--light-text-color);
}
body.light .about {
  background-color: var(--light-bg-color);
}
body.light a, body.light h1, body.light h2, body.light h3, body.light h4, body.light h5, body.light span.name, body.light div {
  color: var(--light-text-color);
}
body.light .theme-toggle__left,
body.light .theme-toggle__right {
  border: 1px solid var(--light-text-color);
  cursor: pointer;
}
body.light .theme-toggle__left {
  width: 8px;
  padding-right: 2px;
  height: 18px;
}
body.light .arrow-up-border {
  border: 1px solid var(--light-text-color);
}
body.light a {
  text-decoration: none;
  color: var(--light-text-color);
}
body.light a:hover {
  text-decoration: underline;
}
body.light ul {
  list-style: none;
}
body.light header,
body.light main,
body.light footer {
  background: var(--light-bg-color);
  color: var(--light-text-color);
}
body.light main,
body.light footer {
  width: 80%;
  margin-left: 10%;
}

#menu-toggle {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-left: 20px;
  cursor: pointer;
}
#menu-toggle .toggle-top,
#menu-toggle .toggle-center,
#menu-toggle .toggle-bottom {
  width: 25px;
  height: 1px;
  padding: 0px;
  transition: 0.5s all ease-in-out;
}
#menu-toggle .toggle-top {
  margin-bottom: 5px;
}
#menu-toggle .toggle-center {
  margin-bottom: 5px;
}
#menu-toggle .toggle-bottom {
  margin-bottom: -0.2px;
}

#menu-toggle.active .toggle-top {
  transform: rotate(45deg);
  transition: 0.5s all ease-in-out;
  margin: 0px;
}
#menu-toggle.active .toggle-center {
  display: none;
}
#menu-toggle.active .toggle-bottom {
  transform: rotate(-45deg);
  margin: 0px;
  transition: 0.5s all ease-in-out;
}

.about {
  height: 100%;
  width: 100%;
  z-index: 999999;
  position: fixed;
}
.about__wrapper {
  display: flex;
  height: inherit;
  width: inherit;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.about__wrapper h2 {
  width: 420px;
  font-weight: 500;
  font-size: 32px;
  line-height: 40px;
}

.about.unactive {
  margin-left: -10000px;
  transition: 0.5s all ease-in-out;
}

.about.active {
  margin-left: 0px;
  transition: 0.5s all ease-in-out;
}

div.theme-toggle {
  width: 20px;
  height: 20px;
  display: inline-flex;
}
div.theme-toggle__left {
  width: 8px;
  height: 18px;
  border-radius: 12px 0 0 12px;
  background-color: #fff;
}
div.theme-toggle__right {
  width: 8px;
  height: 18px;
  border-radius: 0 12px 12px 0;
  background-color: #000;
}

:root {
  --light-text-color: #000;
  --light-bg-color: #fff;
  --dark-text-color: #fff;
  --dark-bg-color: #000;
}

header {
  width: 90%;
  display: inline-flex;
  position: fixed;
  z-index: 999999999999999;
  top: 0;
  left: 5%;
  justify-content: space-between;
  padding: 20px;
}
header .logo {
  width: 80px;
}
header .toggles {
  width: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

main {
  display: flex;
  flex-direction: column;
  float: left;
  margin-top: 30px;
}
main h1 {
  width: 1032px;
  font-weight: 500;
  font-size: 82px;
  color: var(--text-color);
}
main .paintings {
  height: 2000px;
}
main .paintings__row {
  display: inline-flex;
  justify-content: space-between;
}
main .paintings__row h2 {
  margin-top: 0px;
  line-height: 24px;
}
main .paintings__row h2 a .number, main .paintings__row h2 a .name {
  font-weight: 400;
}
main .paintings__row h2 a .number {
  font-size: 18px;
  opacity: 0.5;
}
main .paintings__row h2 a .name {
  font-size: 19px;
}
main .paintings__row h2 a .arrow-right {
  display: block;
  margin: -23px 240px;
  width: 15px;
  height: 15px;
  border-top: 2px solid var(--text-color);
  border-left: 2px solid var(--text-color);
  transform: rotate(135deg);
}
main .paintings__row h2 a .arrow-right::after {
  content: "";
  display: block;
  width: 2px;
  height: 45px;
  background-color: var(--text-color);
  transform: rotate(-45deg) translate(15px, 4px);
  left: 0;
  top: 0;
}
main .paintings .row1 {
  width: 1300px;
  margin-left: 10px;
}
main .paintings .row1__left {
  width: 548px;
}
main .paintings .row1__left img {
  width: 548px;
}
main .paintings .row1__left h2 {
  width: 270px;
}
main .paintings .row1__right {
  width: 548px;
  margin-top: 30px;
}
main .paintings .row1__right img {
  width: 548px;
  height: 430px;
}
main .paintings .row1__right h2 {
  width: 255px;
}
main .paintings .row1__right h2 .arrow-right {
  margin-left: 225px;
}
main .paintings .row2 {
  width: 1200px;
  height: 750px;
  margin-top: 70px;
  margin-left: 300px;
}
main .paintings .row2__left img {
  width: 402px;
}
main .paintings .row2__left h2 {
  width: 265px;
}
main .paintings .row2__left h2 .arrow-right {
  margin-left: 230px;
}
main .paintings .row2__right {
  width: 500px;
  height: 702px;
}
main .paintings .row2__right h2 {
  width: 390px;
}
main .paintings .row2__right h2 .arrow-right {
  margin-left: 365px;
}
main .paintings .row3 {
  width: 1063px;
  height: 675px;
  margin-left: 180px;
  margin-top: 0px;
}
main .paintings .row3__left img {
  width: 448px;
  height: 350px;
}
main .paintings .row3__left h2 {
  width: 448px;
}
main .paintings .row3__left h2 .arrow-right {
  margin-left: 430px;
}
main .paintings .row3__right {
  width: 550px;
  margin-top: 60px;
}
main .paintings .row3__right h2 {
  width: 348px;
}
main .paintings .row3__right h2 .arrow-right {
  margin-left: 326px;
}
main .bio h3, main .bio p {
  width: 1024px;
  font-weight: 500;
}
main .bio h3 {
  font-size: 72px;
  line-height: 80px;
  margin-bottom: 0px;
  margin-top: 150px;
}
main .bio p {
  font-size: 32px;
  line-height: 40px;
  margin-top: 20px;
}
main .bio__dates {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 80px;
}
main .bio__dates img {
  width: 447px;
  height: 631px;
}
main .bio__dates ul {
  width: 420px;
  height: 320px;
  padding-left: 0px;
}
main .bio__dates ul li {
  justify-content: space-between;
  height: 54px;
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
}
main .bio__dates ul li .text {
  flex-direction: column;
  align-items: flex-start;
}
main .bio__dates ul li .text h4, main .bio__dates ul li .text h5 {
  margin: 0px;
}
main .bio__dates ul li .text h4 {
  font-weight: 500;
  font-size: 24px;
}
main .bio__dates ul li .text h5 {
  font-weight: 400;
}
main .bio__dates ul li .number {
  font-weight: 500;
  font-size: 24px;
  opacity: 0.5;
}

.footer {
  margin-top: 50px;
  display: inline-flex;
  justify-content: space-between;
}
.footer__left {
  font-weight: 400;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer__right {
  width: 750px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.footer__right ul {
  display: flex;
  flex-direction: row;
  width: 700px;
  padding: 0;
}
.footer__right ul li {
  width: 110px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 15px;
}
.footer__right ul li a {
  display: flex;
  width: inherit;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 5px;
}
.footer__right ul li a span {
  font-weight: 400;
  font-size: 15px;
}
.footer__right ul li a div {
  width: 20px;
  transform: rotate(-45deg);
  margin-top: 0px;
}
.footer__right ul li a:hover {
  text-decoration: none;
}
.footer__right ul li.border.left {
  border-left: 1px solid var(--text-color);
}
.footer__right ul li.border.right.left {
  border-left: 1px solid var(--text-color);
  border-right: 1px solid var(--text-color);
}
.footer__right ul li.border.right {
  border-right: 1px solid var(--text-color);
  padding: 0 15px;
}
.footer__right .arrow-up-border {
  width: 26px;
  height: 26px;
  border: 1px solid var(--text-color);
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__right .arrow-up-border .arrow-up {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotate(-90deg);
}